body {
  justify-content: space-between;
  align-items: space-between;
  zoom: 100%;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-image: url("LiberTu.100e1003.jpg");
  height: 100vh;
  margin: 0;
  display: flex;
  overflow: hidden;
}

.button-container {
  justify-content: space-between;
  align-items: space-between;
  flex-wrap: wrap;
  width: 100%;
  display: flex;
}

.button {
  box-sizing: border-box;
  cursor: pointer;
  text-align: center;
  background-image: linear-gradient(#ffffff80, #d3d3d380);
  width: 33.3333vw;
  height: 50vh;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 30px;
  font-weight: bold;
  position: absolute;
  overflow: hidden;
}

.button img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.button.combined {
  height: 100vh;
}

.liber-tu {
  justify-content: space-between;
  align-items: space-between;
  flex-wrap: wrap;
  width: 100%;
  display: flex;
}

.canvas-overlay {
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  text-align: center;
  color: #000;
  background-color: #ffffff40;
  border-radius: 5px;
  outline: 3px solid #00000086;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40vw;
  height: 40vh;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 35px;
  font-weight: bold;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.canvas-overlay img {
  width: 80%;
  height: auto;
}

.button:nth-child(2) {
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
}

.button:nth-child(4), .button:nth-child(5) {
  position: absolute;
  bottom: 0;
}

.button:first-child, .button:nth-child(3) {
  position: absolute;
  top: 0;
}

.button:first-child {
  left: 0;
}

.button:nth-child(3) {
  right: 0;
}

.button:nth-child(4) {
  left: 0;
}

.button:nth-child(5) {
  right: 0;
}

.button:hover {
  background-image: linear-gradient(#fffc, #d3d3d3cc);
}

.button:hover img {
  filter: blur(5px);
}

.button:hover:before {
  content: attr(data-hover-text);
  color: #fff;
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 30px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button:first-child:hover:before {
  content: "Orizontal";
}

.button:nth-child(2):hover {
  background-image: linear-gradient(#ffffff80, #d3d3d380);
}

.button:nth-child(2):hover img {
  filter: none;
}

.button:nth-child(2):hover:before {
  content: "";
}

.button:nth-child(3):hover:before {
  content: "Vertical";
}

.button:nth-child(4):hover:before {
  content: "Lux";
}

.button:nth-child(5):hover:before {
  content: "Jaluzele";
}

