body {
    margin: 0;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: space-between;
    height: 100vh;
    background-image: url('../images/LiberTu.jpg');
    zoom: 100%;
    backdrop-filter: blur(5px);
}

.button-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: space-between;
    width: 100%;
    
}

.button {
    box-sizing: border-box; /* Add box-sizing for accurate sizing */
    width: 33.33333vw; /* Adjusted width to fit within the window */
    height: 50vh; /* Adjusted height to fit within the window height */
    background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(211, 211, 211, 0.5));
    cursor: pointer;
    overflow: hidden;
    text-align: center;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 30px;
    position: absolute; /* Added absolute positioning for the button */
}

.button img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    
}

.button.combined {
    height: 100vh; /* Adjusted width for the combined button */
    
}

.liber-tu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: space-between;
    width: 100%;
}

.canvas-overlay {
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40vw;
    height: 40vh;
    background-color: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(2px); /* Semi-transparent red */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: rgb(0, 0, 0);
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 35px;
    outline: 3px solid rgba(0, 0, 0, 0.527); /* Added black outline */
    
}
.canvas-overlay img {
    width: 80%; /* Adjust the width as needed */
    height: auto; /* Maintain aspect ratio */
}
.button:nth-child(2) {
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
}

.button:nth-child(4),
.button:nth-child(5) {
    position: absolute;
    bottom: 0;
}

.button:nth-child(1),
.button:nth-child(3){
    position: absolute;
    top: 0;
}

.button:nth-child(1) {
    left: 0;
}

.button:nth-child(3) {
    right: 0;
}

.button:nth-child(4) {
    left: 0;
}

.button:nth-child(5) {
    right: 0;
}

.button:hover {
    background-image: linear-gradient(rgba(255, 255, 255, 0.8), rgba(211, 211, 211, 0.8));
}

.button:hover img {
    filter: blur(5px);
}

.button:hover::before {
    content: attr(data-hover-text);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%; /* Center the text vertically within the button */
    left: 50%; /* Center the text horizontally within the button */
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    font-size: 30px;
    color: rgb(255, 255, 255);
    z-index: 1; /* Ensure the text is below the image */
}

.button:nth-child(1):hover::before {
    content: "Orizontal";
}

.button:nth-child(2):hover {
    background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(211, 211, 211, 0.5));
}

.button:nth-child(2):hover img {
    filter: none; /* Remove the blur for Button 2 on hover */
}
.button:nth-child(2):hover::before {
    content: ""; /* No text for Button 2 on hover */
}


.button:nth-child(3):hover::before {
    content: "Vertical";
}

.button:nth-child(4):hover::before {
    content: "Lux";
}

.button:nth-child(5):hover::before {
    content: "Jaluzele";
}
